.modal-right {
  display: block;
  z-index: 999;
  position: fixed;
  top: 0;
  right: 0;
  width: 320px;
  height: 100%; }
  .modal-right--mod {
    background-color: #ffffff; }
  .modal-right__button-back--mod {
    padding: 24px 16px 32px 16px; }
  .modal-right .title {
    font-size: 24px;
    line-height: 32px; }
  .modal-right .modal-form {
    width: 100%;
    padding: 0 16px; }
    .modal-right .modal-form input:nth-child(1) {
      margin-top: 24px; }
    .modal-right .modal-form input,
    .modal-right .modal-form textarea {
      width: 100%;
      height: 48px;
      padding: 0 16px;
      background: #ffffff;
      border: 1px solid #eaeaea;
      border-radius: 8px;
      margin: 0 0 16px 0;
      font-family: "TT Lakes";
      font-size: "16px";
      line-height: "24px";
      letter-spacing: "-0.2px"; }
      .modal-right .modal-form input::-moz-placeholder,
      .modal-right .modal-form textarea::-moz-placeholder {
        color: #B5B6BC; }
      .modal-right .modal-form input:-ms-input-placeholder,
      .modal-right .modal-form textarea:-ms-input-placeholder {
        color: #B5B6BC; }
      .modal-right .modal-form input::placeholder,
      .modal-right .modal-form textarea::placeholder {
        color: #B5B6BC; }
    .modal-right .modal-form textarea {
      height: 118px; }
    .modal-right .modal-form .span-text {
      font-family: "TT Lakes";
      font-size: "14px";
      line-height: "16px"; }
      .modal-right .modal-form .span-text--mod {
        color: #7e7e82; }
    .modal-right .modal-form .button-elips {
      display: block;
      width: 136px;
      height: 32px;
      position: relative;
      top: 18px;
      left: calc(100% - 136px); }
      .modal-right .modal-form .button-elips span {
        margin-left: -18px; }
      .modal-right .modal-form .button-elips::before {
        right: 18px; }
      .modal-right .modal-form .button-elips::after {
        right: 18px; }

@media screen and (max-height: 640px) {
  .modal-right {
    padding-top: 0; }
    .modal-right__button-back--mod {
      padding-top: calc(24px - 24px / 4);
      padding-bottom: calc(32px - 32px / 4); }
    .modal-right .modal-form input:nth-child(1) {
      margin-top: calc(24px - 24px / 4); }
    .modal-right .modal-form input,
    .modal-right .modal-form textarea {
      background: #ffffff;
      border: 1px solid #eaeaea !important;
      border-radius: 8px;
      margin-bottom: calc(16px - 16px / 4); }
    .modal-right .modal-form input {
      height: calc(48px - 48px / 4);
      border: 1px solid; }
    .modal-right .modal-form textarea {
      height: calc(118px - 118px / 4); } }

@media screen and (min-width: 768px) {
  .modal-right {
    width: 440px;
    padding-top: 40px; }
    .modal-right__button-back {
      padding: 0;
      position: absolute;
      left: -72px;
      top: 40px; }
    .modal-right .modal-form {
      padding: 0 32px; } }
