.left-menu {
  display: grid;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 320px;
  max-width: 360px;
  height: 100%;
  grid-template-rows: 89.46px 1fr 40px 52px 40px 48px;
  grid-template-columns: auto;
  grid-template-areas: "top-buttons" "list-menu" "middle-buttons" "email" "tel" "lang";
  overflow: auto; }
  .left-menu--mod {
    background-color: #ffffff;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }
  .left-menu__top-buttons {
    grid-area: top-buttons;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap; }
    .left-menu__top-buttons--mod {
      padding: 0 8px 0 24px; }
  .left-menu__menu {
    grid-area: list-menu; }
    .left-menu__menu--mod {
      padding-top: 10px; }
  .left-menu__list-items {
    list-style: none; }
    .left-menu__list-items--mod {
      padding-bottom: 20px; }
    .left-menu__list-items li {
      height: 26px;
      padding: 4px 0; }
    .left-menu__list-items a {
      display: block;
      padding-left: 24px;
      font-family: "TT Lakes";
      font-size: "16px";
      line-height: "24px";
      letter-spacing: "-0.2px";
      color: #1b1c21;
      text-decoration: none; }
    .left-menu__list-items a:hover,
    .left-menu__list-items a:focus {
      color: #7e7e82; }
    .left-menu__list-items a.active {
      padding-left: 20px;
      color: #7e7e82;
      border-left: 4px outset #41f6d7;
      border-top: 4px outset transparent;
      border-bottom: 4px outset transparent; }
  .left-menu__middle-buttons {
    grid-area: middle-buttons;
    display: flex;
    justify-content: start; }
    .left-menu__middle-buttons--mod {
      padding-left: 24px; }
  .left-menu__email {
    grid-area: email;
    display: flex;
    align-items: center; }
    .left-menu__email a {
      font-family: "TT Lakes";
      font-size: "16px";
      line-height: "24px";
      letter-spacing: "-0.2px";
      text-decoration: none;
      color: #1b1c21; }
    .left-menu__email a:hover {
      text-decoration: underline; }
    .left-menu__email--mod {
      padding-left: 24px; }
  .left-menu__tel {
    grid-area: tel; }
    .left-menu__tel a {
      font-family: "TT Lakes";
      font-size: "24px";
      line-height: "32px";
      letter-spacing: "-0.6px";
      color: #1b1c21;
      font-weight: bold;
      text-decoration: none; }
    .left-menu__tel a:hover {
      text-decoration: underline; }
    .left-menu__tel--mod {
      padding-left: 24px; }
  .left-menu__lang {
    grid-area: lang;
    display: flex;
    justify-content: start;
    align-items: center; }
    .left-menu__lang--mod {
      padding-left: 24px; }
  .left-menu__a-lang {
    font-family: "TT Lakes";
    font-size: "16px";
    line-height: "24px";
    letter-spacing: "-0.2px";
    color: #1b1c21;
    text-decoration: none; }
    .left-menu__a-lang:hover {
      transform: scale(1.2); }
    .left-menu__a-lang--mod {
      padding-right: 7px; }
    .left-menu__a-lang--activ {
      color: #7e7e82; }

/*left-menu*/
@media screen and (min-height: 640px) {
  .left-menu {
    grid-template-rows: 89.46px 1fr 40px 52px 40px 68px; }
    .left-menu li {
      height: 40px;
      padding: 10px 0; } }

@media screen and (min-height: 768px) {
  .left-menu {
    grid-template-rows: 89.46px 1fr 40px 52px 40px 88px; }
    .left-menu li {
      height: 56px;
      padding: 16px 0; } }
