.footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px 16px 19px 16px;
  margin-top: 40px;
  border-top: 1px solid #d9fff5;
  font-family: "TT Lakes";
  font-size: "14px";
  line-height: "16px";
  text-align: center;
  mix-blend-mode: normal;
  opacity: 0.72; }
  .footer--color {
    color: #7e7e82; }
  .footer__left, .footer__center, .footer__right {
    text-align: center;
    flex-basis: 33.33%; }
  .footer__center {
    padding: 16px 0; }

@media screen and (min-width: 768px) {
  .footer {
    flex-direction: row;
    margin-top: 47px;
    padding: 32px 24px 41px 24px; }
    .footer__left {
      text-align: left; }
    .footer__center {
      text-align: center;
      padding: 0; }
    .footer__right {
      text-align: right; } }

@media screen and (min-width: 1120px) {
  .footer {
    margin-top: 50px;
    padding: 32px 32px 21px 32px; } }
