.swiper-container-service {
  height: 222px; }
  .swiper-container-service--mod {
    padding: 16px 0 6px 8px; }
  .swiper-container-service .swiper-slide {
    width: 271px;
    height: 200px;
    padding-right: 15px;
    padding-bottom: 0;
    background-color: transparent; }
    .swiper-container-service .swiper-slide__div-a {
      width: 260px;
      height: 100%;
      border-bottom: 1px solid #eaeaea;
      border-radius: 6px;
      position: relative;
      background-color: #ffffff;
      padding: 16px;
      display: flex;
      flex-direction: column;
      justify-content: space-between; }
      .swiper-container-service .swiper-slide__div-a .paragraf {
        position: relative; }
      .swiper-container-service .swiper-slide__div-a span {
        display: block;
        font-family: "TT Lakes";
        font-size: "14px";
        line-height: "18px";
        letter-spacing: "0.2px";
        color: #1b1c21; }
      .swiper-container-service .swiper-slide__div-a .span-razdel {
        line-height: 16px;
        color: #7e7e82;
        mix-blend-mode: normal;
        opacity: 0.72; }
    .swiper-container-service .swiper-slide:last-child .swiper-slide__div-a {
      border-bottom: none; }
  .swiper-container-service .menu__block-right {
    height: 100%;
    z-index: 20; }

.swiper-pagination-service {
  position: static;
  text-align: center; }

.button-elips {
  width: 119px;
  height: 32px;
  border: none;
  outline: none;
  border-radius: 16px;
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: #ff3e79;
  cursor: pointer;
  color: #ffffff;
  transition-property: opacity;
  transition-duration: 0.5s;
  transition-timing-function: ease; }
  .button-elips span {
    margin-left: -28px;
    font-family: "TT Lakes";
    font-size: "12px !important";
    line-height: "24px !important";
    letter-spacing: "-0.15px";
    text-transform: uppercase;
    color: #ffffff !important; }
  .button-elips:hover, .button-elips:focus {
    opacity: 0.5; }
  .button-elips::before {
    content: "";
    display: block;
    width: 8px;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    right: 16px;
    top: 13px;
    transform: rotate(45deg); }
  .button-elips::after {
    content: "";
    display: block;
    width: 8px;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    right: 16px;
    top: 18px;
    transform: rotate(135deg); }

@media screen and (min-width: 768px) {
  .swiper-container-service {
    height: auto; }
    .swiper-container-service--mod {
      padding: 16px; }
    .swiper-container-service .swiper-wrapper {
      display: flex;
      flex-direction: column;
      border-radius: 6px;
      background-color: #ffffff;
      flex-basis: 100%;
      margin-top: 65px;
      padding: 0 16px; }
    .swiper-container-service .swiper-slide {
      width: 100%;
      flex-grow: 1;
      height: 65px;
      background-color: #ffffff;
      padding: 0; }
      .swiper-container-service .swiper-slide__div-a {
        width: 100%;
        height: 100%;
        border-bottom: 1px solid #eaeaea;
        border-radius: 0;
        position: relative;
        background-color: inherit;
        padding: 0;
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: minmax(400px, 50%) minmax(84px, 120px) minmax(210px, 300px);
        align-items: center; }
        .swiper-container-service .swiper-slide__div-a div {
          position: relative; }
        .swiper-container-service .swiper-slide__div-a .button-elips {
          bottom: -7px; }
      .swiper-container-service .swiper-slide:not(.swiper-slide:first-child) > .swiper-slide__div-a > div > .span-razdel {
        display: none; }
      .swiper-container-service .swiper-slide:first-child > .swiper-slide__div-a > div > .span-razdel {
        position: absolute;
        top: -65px; }
    .swiper-container-service .menu__block-right {
      display: none; } }
