.kom-predl {
  padding: 16px;
  max-width: 450px;
  font-family: "TT Lakes";
  font-size: "14px";
  line-height: "20px";
  letter-spacing: "0.2px"; }
  .kom-predl--color {
    color: #1b1c21; }
  .kom-predl__link {
    display: block;
    font-family: "TT Lakes";
    font-size: "16px";
    line-height: "24px";
    letter-spacing: "-0.2px";
    text-decoration: none;
    opacity: 1;
    padding-top: 16px;
    transition-property: opacity;
    transition-duration: 0.5s;
    transition-timing-function: ease; }
    .kom-predl__link:hover, .kom-predl__link:focus {
      opacity: 0.5; }
    .kom-predl__link span {
      display: inline-block; }
      .kom-predl__link span::before {
        content: "";
        display: block;
        width: 8px;
        height: 2px;
        background-color: #ff3e79;
        position: relative;
        left: 110px;
        top: 11px;
        transform: rotate(45deg); }
      .kom-predl__link span::after {
        content: "";
        display: block;
        width: 8px;
        height: 2px;
        background-color: #ff3e79;
        position: relative;
        left: 110px;
        top: -10px;
        transform: rotate(135deg); }

@media screen and (min-width: 768px) {
  .kom-predl {
    padding: 24px 0 24px 24px; } }

@media screen and (min-width: 1120px) {
  .kom-predl {
    padding: 32px 0 32px 32px; } }
