.brands {
  display: flex;
  flex-direction: column; }
  .brands__content {
    flex-grow: 1; }

.section__title {
  background: linear-gradient(270deg, #ffffff 0%, #f8f8f8 98.61%);
  font-family: "TT Lakes";
  font-size: "16px";
  line-height: "24px";
  letter-spacing: "-0.2px";
  text-transform: uppercase; }
  .section__title--mod {
    margin-top: 16px;
    padding: 16px;
    color: #7e7e82; }

.swiper-container--mod,
.swiper-container-vid--mod {
  padding: 16px 0 6px 16px; }

.swiper-container .swiper-slide,
.swiper-container-vid .swiper-slide {
  width: 255px;
  padding-right: 15px;
  background-color: transparent; }
  .swiper-container .swiper-slide--height-72,
  .swiper-container-vid .swiper-slide--height-72 {
    height: 72px; }
  .swiper-container .swiper-slide--height-160,
  .swiper-container-vid .swiper-slide--height-160 {
    height: 160px; }
  .swiper-container .swiper-slide__div-a,
  .swiper-container-vid .swiper-slide__div-a {
    height: 100%;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    position: relative;
    background-color: #ffffff; }
    .swiper-container .swiper-slide__div-a span,
    .swiper-container-vid .swiper-slide__div-a span {
      display: block;
      padding: 16px;
      font-family: "TT Lakes";
      font-size: "16px";
      line-height: "24px";
      letter-spacing: "-0.2px"; }
    .swiper-container .swiper-slide__div-a .span--color,
    .swiper-container-vid .swiper-slide__div-a .span--color {
      color: #1b1c21; }
  .swiper-container .swiper-slide a,
  .swiper-container-vid .swiper-slide a {
    display: block;
    width: 40px;
    height: 40px;
    text-decoration: none;
    border: 2px solid #ff3e79;
    border-radius: 50%;
    position: absolute;
    left: 184px;
    transition-property: background-color;
    transition-duration: 0.5s;
    transition-timing-function: ease; }
  .swiper-container .swiper-slide.swiper-slide--height-72 a,
  .swiper-container-vid .swiper-slide.swiper-slide--height-72 a {
    top: 16px; }
  .swiper-container .swiper-slide.swiper-slide--height-160 a,
  .swiper-container-vid .swiper-slide.swiper-slide--height-160 a {
    top: 104px; }
  .swiper-container .swiper-slide a:hover,
  .swiper-container-vid .swiper-slide a:hover {
    background-color: #ffeef3; }
  .swiper-container .swiper-slide a::before,
  .swiper-container-vid .swiper-slide a::before {
    content: "";
    display: block;
    width: 8px;
    height: 2px;
    background-color: #ff3e79;
    position: absolute;
    right: 15px;
    top: 16px;
    transform: rotate(45deg); }
  .swiper-container .swiper-slide a::after,
  .swiper-container-vid .swiper-slide a::after {
    content: "";
    display: block;
    width: 8px;
    height: 2px;
    background-color: #ff3e79;
    position: absolute;
    right: 15px;
    top: 21px;
    transform: rotate(135deg); }
  .swiper-container .swiper-slide__lenovo,
  .swiper-container-vid .swiper-slide__lenovo {
    background: url(../img/brands/lenovo.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__samsung,
  .swiper-container-vid .swiper-slide__samsung {
    background: url(../img/brands/samsung.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__apple,
  .swiper-container-vid .swiper-slide__apple {
    background: url(../img/brands/apple.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__bosch,
  .swiper-container-vid .swiper-slide__bosch {
    background: url(../img/brands/bosch.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__hp,
  .swiper-container-vid .swiper-slide__hp {
    background: url(../img/brands/hp.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__acer,
  .swiper-container-vid .swiper-slide__acer {
    background: url(../img/brands/acer.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__viewsonic,
  .swiper-container-vid .swiper-slide__viewsonic {
    background: url(../img/brands/viewsonic.png) no-repeat 16px center #ffffff; }
  .swiper-container .swiper-slide__sony,
  .swiper-container-vid .swiper-slide__sony {
    background: url(../img/brands/sony.png) no-repeat 16px center #ffffff; }

.swiper-container .menu__block-right,
.swiper-container-vid .menu__block-right {
  height: 100%;
  z-index: 20; }

.swiper-pagination,
.swiper-pagination-vid {
  position: static;
  text-align: center; }

@media screen and (min-width: 768px) {
  .brands__content .button-read--mod,
  .vid_remonta .button-read--mod {
    margin-left: 32px; }
  .brands__title--mod {
    padding: 16px 24px; }
  .swiper-container,
  .swiper-container-vid {
    height: 200px;
    overflow: hidden; }
    .swiper-container--mod,
    .swiper-container-vid--mod {
      margin: 0;
      padding: 24px 0 24px 24px; }
    .swiper-container .swiper-wrapper,
    .swiper-container-vid .swiper-wrapper {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      margin: 0;
      padding: 0; }
      .swiper-container .swiper-wrapper:after,
      .swiper-container-vid .swiper-wrapper:after {
        content: "";
        display: block;
        width: 224px;
        height: 1px;
        order: 100;
        margin: 0 24px 16px 0; }
      .swiper-container .swiper-wrapper .swiper-slide,
      .swiper-container-vid .swiper-wrapper .swiper-slide {
        margin: 0 0 16px 0;
        width: 247px;
        padding-right: 23px; }
        .swiper-container .swiper-wrapper .swiper-slide--order-2,
        .swiper-container-vid .swiper-wrapper .swiper-slide--order-2 {
          order: 2; }
        .swiper-container .swiper-wrapper .swiper-slide--order-3, .swiper-container .swiper-wrapper .swiper-slide--order-viewsonic,
        .swiper-container-vid .swiper-wrapper .swiper-slide--order-3,
        .swiper-container-vid .swiper-wrapper .swiper-slide--order-viewsonic {
          order: 3; }
        .swiper-container .swiper-wrapper .swiper-slide a,
        .swiper-container-vid .swiper-wrapper .swiper-slide a {
          left: 158px; }
        .swiper-container .swiper-wrapper .swiper-slide a::before,
        .swiper-container-vid .swiper-wrapper .swiper-slide a::before {
          right: 13px;
          top: 15px; }
        .swiper-container .swiper-wrapper .swiper-slide a::after,
        .swiper-container-vid .swiper-wrapper .swiper-slide a::after {
          right: 13px;
          top: 20px; }
    .swiper-container .menu__block-right,
    .swiper-container-vid .menu__block-right {
      display: none; }
    .swiper-container--height,
    .swiper-container-vid--height {
      height: auto; }
  .swiper-pagination,
  .swiper-pagination-vid {
    display: none; } }

@media screen and (min-width: 964px) {
  .brands__content .swiper-container .swiper-wrapper .swiper-slide--order-viewsonic {
    order: 1; } }

@media screen and (min-width: 1120px) {
  .brands__content .button-read--mod,
  .vid_remonta .button-read--mod {
    margin-left: 39px; }
  .brands__title--mod {
    padding: 16px 32px; }
  .swiper-container--mod,
  .swiper-container-vid--mod {
    padding: 24px 0 24px 32px; }
  .swiper-container .swiper-wrapper::before,
  .swiper-container-vid .swiper-wrapper::before {
    width: 223px;
    margin: 0 32px 16px 0; }
  .swiper-container .swiper-wrapper:after,
  .swiper-container-vid .swiper-wrapper:after {
    width: 240px;
    margin: 0 32px 16px 0; }
  .swiper-container .swiper-wrapper .swiper-slide,
  .swiper-container-vid .swiper-wrapper .swiper-slide {
    width: 271px;
    padding-right: 31px; }
    .swiper-container .swiper-wrapper .swiper-slide--order-viewsonic,
    .swiper-container-vid .swiper-wrapper .swiper-slide--order-viewsonic {
      order: 1; }
    .swiper-container .swiper-wrapper .swiper-slide a,
    .swiper-container-vid .swiper-wrapper .swiper-slide a {
      left: 184px; } }
