@font-face {
  font-family: "TT Lakes";
  src: url(../fonts/TTLakes-Regular.eot);
  src: url(../fonts/TTLakes-Regular.eot?#iefix) format("embedded-opentype"), url(../fonts/TTLakes-Regular.woff) format("woff"), url(../fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "TT Lakes";
  src: url(../fonts/TTLakes-Bold.eot);
  src: url(../fonts/TTLakes-Bold.eot?#iefix) format("embedded-opentype"), url(../fonts/TTLakes-Bold.woff) format("woff"), url(../fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "TT Lakes Medium";
  src: url(../fonts/TTLakes-Medium.eot);
  src: url(../fonts/TTLakes-Medium.eot?#iefix) format("embedded-opentype"), url(../fonts/TTLakes-Medium.woff) format("woff"), url(../fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

HTML,
BODY {
  width: 100%;
  height: 100%;
  background-color: #f8f8f8; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.--main-mod {
  background-color: #f8f8f8;
  padding: 24px 0; }

.main-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  width: 100%; }

.title {
  font-family: "TT Lakes";
  font-size: "28px";
  line-height: "40px";
  letter-spacing: "-0.6px";
  font-weight: bold;
  border-left: 4px outset #41f6d7;
  border-top: 4px outset transparent;
  border-bottom: 4px outset transparent; }
  .title--mod {
    color: #1b1c21;
    padding: 0 0 0 12px; }

.buttons-1120 {
  display: none;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap; }
  .buttons-1120__button {
    display: flex;
    align-items: center;
    height: 40px;
    border: none;
    background: none;
    outline: none;
    cursor: pointer;
    font-family: "TT Lakes";
    font-size: "16px";
    line-height: "24px";
    letter-spacing: "-0.2px";
    color: #1b1c21; }
    .buttons-1120__button img {
      border: none;
      outline: none;
      opacity: 1;
      transition-property: opacity;
      transition-duration: 1s;
      transition-timing-function: ease; }
      .buttons-1120__button img.--mod {
        margin-left: 8px; }
    .buttons-1120__button:hover img, .buttons-1120__button:focus img {
      opacity: 0.5; }
    .buttons-1120__button--mod {
      margin-right: 32px; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  position: relative; }
  .menu--mod {
    margin-top: 24px; }
  .menu__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    scrollbar-width: thin;
    scrollbar-color: #f8f8f8 #f8f8f8; }
    .menu__nav::-webkit-scrollbar {
      display: none; }
    .menu__nav--mod {
      padding: 0 16px; }
  .menu__button {
    height: 40px;
    white-space: nowrap;
    font-family: "TT Lakes";
    font-size: "16px";
    line-height: "24px";
    letter-spacing: "-0.2px";
    border: 2px solid transparent;
    border-radius: 6px;
    outline: none;
    background-color: transparent;
    cursor: pointer; }
    .menu__button--mod {
      padding: 0 9.3px 0 16.54px;
      color: #7e7e82; }
    .menu__button:hover, .menu__button:focus {
      background-color: #ffffff;
      border: 2px solid #b8ffec;
      border-radius: 6px; }
  .menu__block-right {
    height: 40px;
    position: absolute;
    left: 92.5%;
    right: 0;
    top: 0;
    background: linear-gradient(270deg, #f8f8f8 16.84%, rgba(248, 248, 248, 0.0001) 100%); }

/*menu*/
.content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  .content--mod {
    margin-top: 24px; }

.text {
  font-family: "TT Lakes";
  font-size: "14px";
  line-height: "18px";
  letter-spacing: "0.2px"; }
  .text--mod {
    color: #1b1c21;
    padding: 0 16px; }
  .text__txt {
    height: 90px;
    overflow-y: hidden;
    font-weight: normal; }
    .text__txt--mod {
      margin-bottom: 16px; }

.text__txt.text__txt--mod.text__txt--height {
  height: auto; }

.button-read {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 24px;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  font-family: "TT Lakes Medium";
  font-size: "16px";
  line-height: "24px";
  letter-spacing: "-0.2px"; }
  .button-read img {
    margin-right: 16px; }
  .button-read--mod {
    color: #1b1c21;
    padding-left: 8px; }
  .button-read:hover {
    opacity: 0.7; }
  .button-read--pos {
    position: relative;
    top: -24px; }

.foto {
  width: 100%; }
  .foto--mod {
    margin-top: 16px; }
  .foto__img {
    width: 100%; }

.div-modal {
  display: none; }
  .div-modal .div-blur {
    display: block;
    width: 100vw;
    height: 100vh;
    background-color: #ffffff;
    opacity: 0.6;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 888; }
  .div-modal--block {
    display: block; }

@media screen and (max-width: 768px), (min-width: 1600px) {
  .button-read--none {
    display: none; } }

@media screen and (min-width: 768px) {
  .header--mod {
    padding: 0 24px; }
  .header-left {
    width: 192px;
    max-width: 192px; }
  .header-right__left-buttons {
    display: flex; }
  .header__vline--none {
    display: block; }
  .title--mod {
    padding: 0 0 0 20px; }
  .menu__nav--mod {
    padding: 0 24px; }
  .text--mod {
    padding: 0 24px; }
  .text__txt {
    width: 40vw;
    height: 160px; }
  .foto {
    width: 47vw; }
  .foto--mod {
    margin: 0 24px; } }

@media screen and (min-width: 1120px) {
  .header {
    display: none; }
  .--main-mod {
    background-color: #f8f8f8;
    padding-top: 42px; }
  .title--mod {
    padding-left: 32px; }
  .buttons-1120 {
    display: flex; }
  .menu__nav {
    flex-wrap: wrap;
    justify-content: flex-start; }
    .menu__nav--mod {
      padding: 0 32px; }
  .menu__block-right {
    display: none; }
  .text--mod {
    padding: 0 32px; }
  .text__txt {
    width: 44vw;
    height: 160px; }
  .foto--mod {
    margin: 0 32px 0 0; } }

@media screen and (min-width: 1392px) {
  body {
    width: 1392px;
    margin: 0 auto; } }
